// src/components/screens/MarketScreen.tsx
import React, { useState, useEffect } from 'react';
import { User, Music, Headphones, ShoppingBag, Hammer, Coffee, Utensils } from 'lucide-react';
import { ProductType, CategoryType, ExperienceProductType } from '../../types/market';
import { IconProps } from '../../types/common';
import CategoryFilter from '../market/CategoryFilter';
import SearchBar from '../market/SearchBar';
import ProductGrid from '../market/ProductGrid';
import ProductRecommendation from '../market/ProductRecommendation';

const MarketScreen: React.FC = () => {
  const [selectedCategory, setSelectedCategory] = useState('all');
  const [searchQuery, setSearchQuery] = useState('');
  const [loading, setLoading] = useState(false);
  
  // 
  const categories: CategoryType[] = [
    { id: 'food', name: '' },
    { id: 'craft', name: '' },
    { id: 'beverage', name: '' },
    { id: 'fashion', name: '' },
    { id: 'art', name: '' },
    { id: 'beauty', name: '' },
  ];
  
  // 
  const marketItems: ProductType[] = [
    {
      id: 1,
      name: '',
      price: 8500,
      description: '',
      attender: ' ',
      region: '',
      category: 'craft',
      icon: <Hammer size={24} />,
      rating: 4.8,
      reviewCount: 42,
      featured: true
    },
    {
      id: 2,
      name: '6',
      price: 3600,
      description: '',
      attender: ' ',
      region: '',
      category: 'beverage',
      icon: <Coffee size={24} />,
      rating: 4.7,
      reviewCount: 36,
      limited: true
    },
    {
      id: 3,
      name: '',
      price: 5800,
      description: '',
      attender: ' ',
      region: '',
      category: 'food',
      icon: <Utensils size={24} />,
      rating: 4.9,
      reviewCount: 58
    },
    {
      id: 4,
      name: '',
      price: 12800,
      description: '',
      attender: ' ',
      region: '',
      category: 'fashion',
      icon: <ShoppingBag size={24} />,
      rating: 4.6,
      reviewCount: 24
    },
    {
      id: 5,
      name: '',
      price: 18000,
      description: '50',
      attender: ' ',
      region: '',
      category: 'art',
      icon: <Hammer size={24} />,
      rating: 4.9,
      reviewCount: 17,
      limited: true
    },
    {
      id: 6,
      name: '',
      price: 4500,
      description: '3',
      attender: ' ',
      region: '',
      category: 'food',
      icon: <Coffee size={24} />,
      rating: 4.8,
      reviewCount: 62
    },
    {
      id: 7,
      name: '',
      price: 3200,
      description: '',
      attender: ' ',
      region: '',
      category: 'fashion',
      icon: <ShoppingBag size={24} />,
      rating: 4.7,
      reviewCount: 29
    },
    {
      id: 8,
      name: '',
      price: 2800,
      description: '5',
      attender: ' ',
      region: '',
      category: 'beauty',
      icon: <Coffee size={24} />,
      rating: 4.5,
      reviewCount: 41
    }
  ];

  // 
  const experienceProducts: ExperienceProductType = {
    experienceId: "123",
    experienceName: '',
    attenderName: ' ',
    products: [
      {
        id: 101,
        name: '',
        price: 3200,
        description: '',
        attender: ' ',
        region: '',
        category: 'art',
        icon: <Music size={24} />,
        rating: 4.8,
        reviewCount: 15
      },
      {
        id: 102,
        name: '',
        price: 4500,
        description: '5CD',
        attender: ' ',
        region: '',
        category: 'art',
        icon: <Headphones size={24} />,
        rating: 4.7,
        reviewCount: 8
      },
      {
        id: 103,
        name: 'T',
        price: 3500,
        description: 'T',
        attender: ' ',
        region: '',
        category: 'fashion',
        icon: <ShoppingBag size={24} />,
        rating: 4.6,
        reviewCount: 12
      },
      {
        id: 104,
        name: '',
        price: 1800,
        description: '',
        attender: ' ',
        region: '',
        category: 'beverage',
        icon: <Coffee size={24} />,
        rating: 4.9,
        reviewCount: 23
      }
    ]
  };

  // 
  const handleSearch = (query: string) => {
    setSearchQuery(query);
    // API
    setLoading(true);
    setTimeout(() => {
      setLoading(false);
    }, 500);
  };

  // 
  const filteredProducts = marketItems.filter(item => {
    // 
    const categoryMatch = selectedCategory === 'all' || item.category === selectedCategory;
    
    // 
    const searchMatch = !searchQuery || 
      item.name.toLowerCase().includes(searchQuery.toLowerCase()) ||
      item.description.toLowerCase().includes(searchQuery.toLowerCase()) ||
      item.attender.toLowerCase().includes(searchQuery.toLowerCase()) ||
      item.region.toLowerCase().includes(searchQuery.toLowerCase());
    
    return categoryMatch && searchMatch;
  });
  
  // 
  const featuredProducts = marketItems.filter(item => item.featured);
  
  // 
  const limitedProducts = marketItems.filter(item => item.limited);

  // 
  useEffect(() => {
    // API
    // fetchProducts();
  }, []);
  
  return (
    <div className="pb-16 space-y-6">
      {/*  */}
      <div className="sticky top-0 z-10 bg-mono-white p-4 shadow-sm">
        <h1 className="text-2xl font-bold mb-3 text-mono-black"></h1>
        
        {/*  */}
        <SearchBar onSearch={handleSearch} placeholder="..." />
        
        {/*  */}
        <div className="mt-3">
          <CategoryFilter 
            categories={categories} 
            selectedCategory={selectedCategory} 
            onCategoryChange={setSelectedCategory} 
          />
        </div>
      </div>
      
      <div className="px-4 space-y-8">
        {/*  */}
        <ProductRecommendation
          title=""
          products={experienceProducts.products}
          experienceId={experienceProducts.experienceId}
          experienceName={experienceProducts.experienceName}
          attenderName={experienceProducts.attenderName}
        />
        
        {/*  */}
        {limitedProducts.length > 0 && (
          <div>
            <div className="flex justify-between items-center mb-3">
              <h2 className="text-xl font-bold text-mono-black"></h2>
              <button className="text-sm text-mono-black font-medium">
                
              </button>
            </div>
            <div className="overflow-x-auto pb-4">
              <div className="flex space-x-3" style={{ minWidth: 'min-content' }}>
                {limitedProducts.map(product => (
                  <div 
                    key={product.id}
                    className="w-40 flex-shrink-0 bg-mono-white rounded-lg overflow-hidden shadow-sm"
                  >
                    <div className="h-40 bg-mono-lighter relative flex items-center justify-center">
                      {product.icon && React.cloneElement(product.icon as React.ReactElement<IconProps>, { size: 32, className: "text-mono-gray-medium" })}
                      <div className="absolute top-2 left-2 bg-mono-black text-mono-white text-xs py-1 px-2 rounded-full">
                        
                      </div>
                    </div>
                    <div className="p-3">
                      <p className="font-medium line-clamp-2 text-mono-black">{product.name}</p>
                      <p className="text-mono-black font-bold mt-1">{product.price.toLocaleString()}</p>
                    </div>
                  </div>
                ))}
              </div>
            </div>
          </div>
        )}
        
        {/*  */}
        <div>
          <div className="flex justify-between items-center mb-3">
            <h2 className="text-xl font-bold text-mono-black"></h2>
            <button className="text-sm text-mono-black font-medium">
              
            </button>
          </div>
          
          {loading ? (
            <div className="py-8 text-center text-mono-gray-medium">...</div>
          ) : (
            searchQuery ? (
              <>
                <p className="mb-3 text-sm text-mono-gray-medium">{searchQuery}: {filteredProducts.length}</p>
                <ProductGrid products={filteredProducts} category={selectedCategory} />
              </>
            ) : (
              <ProductGrid products={marketItems} category={selectedCategory} />
            )
          )}
        </div>
        
        {/*  */}
        <div className="bg-mono-black rounded-lg p-4 text-mono-white">
          <h3 className="font-bold text-lg mb-2"></h3>
          <p className="text-sm mb-3">
            
          </p>
          <button className="bg-mono-white text-mono-black font-medium py-2 px-4 rounded-lg text-sm hover:bg-mono-lighter transition-colors duration-200">
            
          </button>
        </div>
      </div>
    </div>
  );
};

export default MarketScreen;