// src/components/screens/SeasonalEventsScreen.tsx
import React, { useState, useEffect } from 'react';
import { Calendar, Sunrise, Coffee, Moon, Compass, ArrowRight } from 'lucide-react';
import { EventType, FilterOptionType } from '../../types/event';
import EventList from '../event/EventList';
import EventCard from '../event/EventCard';
import EventFilter from '../event/EventFilter';

const SeasonalEventsScreen: React.FC = () => {
  const [viewType, setViewType] = useState('list'); // 'list' or 'calendar'
  const [loading, setLoading] = useState(false);
  const [selectedFilters, setSelectedFilters] = useState({
    type: 'all',
    period: 'all'
  });
  
  // 
  const filterOptions = {
    types: [
      { id: 'morning', name: '' },
      { id: 'afternoon', name: '' },
      { id: 'evening', name: '' },
      { id: 'night', name: '' },
      { id: 'workshop', name: '' }
    ] as FilterOptionType[],
    periods: [
      { id: 'oneday', name: '1' },
      { id: 'weekend', name: '' },
      { id: 'weekly', name: '' },
      { id: 'monthly', name: '' },
      { id: 'seasonal', name: '' }
    ] as FilterOptionType[]
  };
  
  // 
  const seasonalEvents: EventType[] = [
    {
      id: 1,
      day: '15',
      title: '',
      time: '5:008:00',
      attender: ' ',
      period: '7',
      note: '',
      price: 5800,
      location: '',
      description: '',
      icon: <Sunrise size={24} />,
      type: 'morning',
      rating: 4.9,
      reviewCount: 32,
      featured: true
    },
    {
      id: 2,
      day: '20',
      title: '',
      time: '18:0021:00',
      attender: ' ',
      period: '',
      note: '',
      price: 4800,
      location: '',
      description: '',
      icon: <Moon size={24} />,
      type: 'evening',
      rating: 4.7,
      reviewCount: 56
    },
    {
      id: 3,
      day: '25',
      title: '',
      time: '20:0022:00',
      attender: ' ',
      period: '',
      note: '',
      price: 3500,
      location: '',
      description: '',
      icon: <Moon size={24} />,
      type: 'night',
      rating: 4.8,
      reviewCount: 24
    },
    {
      id: 4,
      day: '18',
      title: '',
      time: '9:0013:00',
      attender: ' ',
      period: '',
      note: '',
      price: 6800,
      location: '',
      description: '',
      icon: <Coffee size={24} />,
      type: 'morning',
      rating: 4.9,
      reviewCount: 48
    },
    {
      id: 5,
      day: '10',
      title: '',
      time: '19:3021:30',
      attender: ' ',
      period: '67',
      note: '',
      price: 3800,
      location: '',
      description: '',
      icon: <Moon size={24} />,
      type: 'night',
      rating: 4.8,
      reviewCount: 37
    },
  ];

  // 
  const timeFrameExperiences = [
    {
      id: 101,
      title: '',
      time: '5:007:00',
      period: '',
      price: 4500,
      type: 'morning',
      icon: <Sunrise size={24} />,
      day: '',
      attender: ' ',
      rating: 4.9,
      reviewCount: 27
    },
    {
      id: 102,
      title: '',
      time: '14:0016:00',
      period: '',
      price: 3800,
      type: 'afternoon',
      icon: <Coffee size={24} />,
      day: '',
      attender: ' ',
      rating: 4.8,
      reviewCount: 42
    },
    {
      id: 103,
      title: '',
      time: '19:0021:00',
      period: '',
      price: 4000,
      type: 'night',
      icon: <Moon size={24} />,
      day: '',
      attender: ' ',
      rating: 4.7,
      reviewCount: 36
    },
    {
      id: 104,
      title: '',
      time: '13:0015:30',
      period: '',
      price: 3500,
      type: 'afternoon',
      icon: <Compass size={24} />,
      day: '',
      attender: ' ',
      rating: 4.9,
      reviewCount: 64
    }
  ];

  // 
  const featuredEvent: EventType = {
    id: 201,
    title: '',
    day: '2',
    month: '7',
    time: '18:0021:00',
    attender: ' ',
    period: '7277',
    price: 4500,
    location: '',
    description: '',
    badge: '',
    featured: true,
    icon: <Calendar size={24} />,
    rating: 4.9,
    reviewCount: 28
  };

  // 
  const handleFilterChange = (filterType: 'type' | 'period', value: string) => {
    setSelectedFilters(prev => ({
      ...prev,
      [filterType]: value
    }));
    
    // 
    setLoading(true);
    setTimeout(() => {
      setLoading(false);
    }, 500);
  };
  
  const clearFilters = () => {
    setSelectedFilters({
      type: 'all',
      period: 'all'
    });
  };
  
  // 
  const filteredEvents = seasonalEvents.filter(event => {
    const typeMatch = selectedFilters.type === 'all' || event.type === selectedFilters.type;
    const periodMatch = selectedFilters.period === 'all' || (
      event.period && event.period.includes(
        filterOptions.periods.find(p => p.id === selectedFilters.period)?.name || ''
      )
    );
    
    return typeMatch && periodMatch;
  });
  
  useEffect(() => {
    // API
    // fetchEvents();
  }, []);
  
  return (
    <div className="pb-16 space-y-6">
      {/*  */}
      <div className="sticky top-0 z-10 bg-white p-4 shadow-sm">
        <div className="flex justify-between items-center mb-3">
          <h1 className="text-2xl font-bold"></h1>
          <div className="flex space-x-1 bg-gray-100 rounded-lg p-1">
            <button 
              onClick={() => setViewType('list')}
              className={`px-3 py-1 rounded text-sm ${
                viewType === 'list' ? 'bg-white shadow-sm' : ''
              }`}
            >
              
            </button>
            <button 
              onClick={() => setViewType('calendar')}
              className={`px-3 py-1 rounded text-sm ${
                viewType === 'calendar' ? 'bg-white shadow-sm' : ''
              }`}
            >
              
            </button>
          </div>
        </div>
        
        {/*  */}
        <EventFilter 
          filters={filterOptions}
          selectedFilters={selectedFilters}
          onFilterChange={handleFilterChange}
          onClearFilters={clearFilters}
        />
      </div>
      
      <div className="px-4 space-y-8">
        {/*  */}
        <div className="bg-gradient-to-r from-gray-800 to-black rounded-lg p-4 text-white relative overflow-hidden">
          <div className="relative z-10">
            <div className="flex items-center mb-2">
              <Calendar size={20} className="mr-2" />
              <span className="font-medium">{featuredEvent.period}</span>
            </div>
            <h2 className="text-xl font-bold mb-1">{featuredEvent.title}</h2>
            <p className="text-sm opacity-90 mb-3">
              
            </p>
            <button className="bg-white text-black font-medium py-2 px-4 rounded-lg text-sm">
              
            </button>
          </div>
          <div className="absolute top-0 right-0 opacity-20 text-9xl">
            
          </div>
        </div>
        
        {/*  */}
        <div>
          <div className="flex justify-between items-center mb-3">
            <h2 className="text-xl font-bold"></h2>
            <button className="flex items-center text-sm font-medium">
              
              <ArrowRight size={16} className="ml-1" />
            </button>
          </div>
          <div className="grid grid-cols-2 gap-3">
            {timeFrameExperiences.map(experience => (
              <EventCard 
                key={experience.id}
                event={experience}
                variant="compact"
              />
            ))}
          </div>
        </div>
        
        {/*  */}
        <div>
          <div className="flex justify-between items-center mb-3">
            <h2 className="text-xl font-bold"></h2>
            {(selectedFilters.type !== 'all' || selectedFilters.period !== 'all') && (
              <p className="text-sm text-gray-600">
                {filteredEvents.length}
              </p>
            )}
          </div>
          
          {loading ? (
            <div className="py-8 text-center text-gray-500">...</div>
          ) : viewType === 'list' ? (
            <EventList events={filteredEvents} />
          ) : (
            <div className="grid grid-cols-1 gap-4 md:grid-cols-2">
              {filteredEvents.map(event => (
                <EventCard key={event.id} event={event} />
              ))}
              {filteredEvents.length === 0 && (
                <div className="col-span-full py-8 text-center text-gray-500">
                  
                </div>
              )}
            </div>
          )}
        </div>
        
        {/*  */}
        <div className="bg-gray-50 rounded-lg p-4">
          <h3 className="font-bold text-lg mb-2"></h3>
          <p className="text-sm text-gray-700 mb-3">
            
          </p>
          <button className="bg-black text-white font-medium py-2 px-4 rounded-lg text-sm w-full">
            
          </button>
        </div>
      </div>
    </div>
  );
};

export default SeasonalEventsScreen;