import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { useAuth } from '../../../AuthComponents';
import { createExperience } from '../../../services/AttenderService';
import { ChevronLeft, Camera, Clock, MapPin, Tag, Check, Loader, FileText, Calendar, DollarSign } from 'lucide-react';
import FileUploader from '../../common/FileUploader';

const CreateExperienceForm: React.FC = () => {
  const navigate = useNavigate();
  const { user } = useAuth();
  const [loading, setLoading] = useState(false);
  const [success, setSuccess] = useState(false);
  const [error, setError] = useState<string | null>(null);
  
  const [formData, setFormData] = useState({
    title: '',
    description: '',
    imageUrl: '',
    duration: 60,
    price: 5000,
    categories: [] as string[],
    location: '',
    availableDays: [] as number[],
    maxParticipants: 4,
    includesItems: '',
    requiredItems: '',
    cancellationPolicy: 'moderate' // 'flexible', 'moderate', 'strict'
  });

  // 
  const categoryOptions = [
    '', '', '', 
    '', '', '',
    '', '', ''
  ];
  
  // 
  const daysOfWeek = ['', '', '', '', '', '', ''];

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement | HTMLSelectElement>) => {
    const { name, value } = e.target;
    setFormData({ ...formData, [name]: value });
  };

  const handleNumberInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target;
    setFormData({ ...formData, [name]: parseInt(value) || 0 });
  };

  const handleCategoryToggle = (category: string) => {
    const updatedCategories = formData.categories.includes(category)
      ? formData.categories.filter(c => c !== category)
      : [...formData.categories, category];
    
    setFormData({ ...formData, categories: updatedCategories });
  };

  const handleDayToggle = (day: number) => {
    const updatedDays = formData.availableDays.includes(day)
      ? formData.availableDays.filter(d => d !== day)
      : [...formData.availableDays, day];
    
    setFormData({ ...formData, availableDays: updatedDays });
  };

  const handleImageUpload = (url: string) => {
    setFormData({ ...formData, imageUrl: url });
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    
    if (!user?.id) {
      setError('');
      return;
    }
    
    try {
      setLoading(true);
      setError(null);
      
      // 
      if (!formData.title) throw new Error('');
      if (!formData.description) throw new Error('');
      if (formData.categories.length === 0) throw new Error('');
      if (formData.availableDays.length === 0) throw new Error('');
      
      await createExperience(user.id, formData);
      setSuccess(true);
      
      // 
      setTimeout(() => {
        navigate('/attender/experiences');
      }, 2000);
    } catch (err) {
      setError(err instanceof Error ? err.message : '');
      console.error('Failed to create experience:', err);
    } finally {
      setLoading(false);
    }
  };

  const handleCancel = () => {
    // 
    navigate('/profile');
  };

  return (
    <div className="max-w-3xl mx-auto px-4 py-8">
      <button 
        onClick={() => navigate(-1)}
        className="flex items-center text-gray-600 mb-6"
      >
        <ChevronLeft size={20} />
        <span></span>
      </button>
      
      <h1 className="text-2xl font-bold mb-6"></h1>
      
      {error && (
        <div className="bg-red-50 p-4 rounded-lg text-red-700 mb-6">
          {error}
        </div>
      )}
      
      {success && (
        <div className="bg-green-50 p-4 rounded-lg text-green-700 mb-6 flex items-center">
          <Check className="w-5 h-5 mr-2" />
          ...
        </div>
      )}
      
      <form onSubmit={handleSubmit} className="space-y-6">
        {/*  */}
        <div className="bg-white rounded-lg shadow p-6">
          <h2 className="text-xl font-bold mb-4"></h2>
          
          {/*  */}
          <div className="mb-4">
            <label htmlFor="title" className="block text-sm font-medium text-gray-700 mb-1">
              <span className="text-red-500 ml-1">*</span>
            </label>
            <input
              id="title"
              name="title"
              type="text"
              value={formData.title}
              onChange={handleInputChange}
              className="w-full px-3 py-2 border border-gray-300 rounded-md"
              placeholder=": "
              required
            />
          </div>
          
          {/*  */}
          <div className="mb-4">
            <label htmlFor="description" className="block text-sm font-medium text-gray-700 mb-1">
              <span className="text-red-500 ml-1">*</span>
            </label>
            <textarea
              id="description"
              name="description"
              value={formData.description}
              onChange={handleInputChange}
              className="w-full px-3 py-2 border border-gray-300 rounded-md"
              rows={6}
              placeholder=""
              required
            />
          </div>
          
          {/*  */}
          <div className="mb-4">
            <label className="block text-sm font-medium text-gray-700 mb-1">
              
            </label>
            
            <div className="flex items-start">
              <div className="relative w-32 h-24 bg-gray-100 rounded overflow-hidden mr-4">
                {formData.imageUrl ? (
                  <img
                    src={formData.imageUrl}
                    alt=""
                    className="w-full h-full object-cover"
                  />
                ) : (
                  <div className="w-full h-full flex items-center justify-center text-gray-400">
                    <Camera className="w-8 h-8" />
                  </div>
                )}
              </div>
              
              <div>
                <FileUploader
                  onUploadComplete={handleImageUpload}
                  acceptedFileTypes="image/*"
                  buttonLabel=""
                />
                <p className="text-xs text-gray-500 mt-1">
                   1200800px
                </p>
              </div>
            </div>
          </div>
          
          {/*  */}
          <div className="mb-4">
            <label className="block text-sm font-medium text-gray-700 mb-1">
              <span className="text-red-500 ml-1">*</span>
            </label>
            
            <div className="flex flex-wrap gap-2">
              {categoryOptions.map((category) => (
                <button
                  key={category}
                  type="button"
                  onClick={() => handleCategoryToggle(category)}
                  className={`px-3 py-1 rounded-full text-sm flex items-center ${
                    formData.categories.includes(category)
                      ? 'bg-blue-600 text-white'
                      : 'bg-gray-100 text-gray-700 hover:bg-gray-200'
                  }`}
                >
                  <Tag size={14} className="mr-1" />
                  {category}
                </button>
              ))}
            </div>
          </div>
        </div>
        
        {/*  */}
        <div className="bg-white rounded-lg shadow p-6">
          <h2 className="text-xl font-bold mb-4"></h2>
          
          {/*  */}
          <div className="mb-4">
            <label htmlFor="location" className="block text-sm font-medium text-gray-700 mb-1">
              <span className="text-red-500 ml-1">*</span>
            </label>
            <div className="flex">
              <span className="inline-flex items-center px-3 border border-r-0 border-gray-300 bg-gray-50 text-gray-500 rounded-l-md">
                <MapPin size={16} />
              </span>
              <input
                id="location"
                name="location"
                type="text"
                value={formData.location}
                onChange={handleInputChange}
                className="flex-1 px-3 py-2 border border-gray-300 rounded-r-md"
                placeholder=":  - "
                required
              />
            </div>
          </div>
          
          {/*  */}
          <div className="mb-4">
            <label htmlFor="duration" className="block text-sm font-medium text-gray-700 mb-1">
              <span className="text-red-500 ml-1">*</span>
            </label>
            <div className="flex">
              <span className="inline-flex items-center px-3 border border-r-0 border-gray-300 bg-gray-50 text-gray-500 rounded-l-md">
                <Clock size={16} />
              </span>
              <input
                id="duration"
                name="duration"
                type="number"
                min="30"
                max="480"
                step="30"
                value={formData.duration}
                onChange={handleNumberInputChange}
                className="w-24 px-3 py-2 border border-gray-300"
              />
              <span className="inline-flex items-center px-3 border border-l-0 border-gray-300 bg-gray-50 text-gray-500 rounded-r-md">
                
              </span>
            </div>
          </div>
          
          {/*  */}
          <div className="mb-4">
            <label htmlFor="price" className="block text-sm font-medium text-gray-700 mb-1">
              1<span className="text-red-500 ml-1">*</span>
            </label>
            <div className="flex">
              <span className="inline-flex items-center px-3 border border-r-0 border-gray-300 bg-gray-50 text-gray-500 rounded-l-md">
                <DollarSign size={16} />
              </span>
              <input
                id="price"
                name="price"
                type="number"
                min="1000"
                step="100"
                value={formData.price}
                onChange={handleNumberInputChange}
                className="w-32 px-3 py-2 border border-gray-300"
              />
              <span className="inline-flex items-center px-3 border border-l-0 border-gray-300 bg-gray-50 text-gray-500 rounded-r-md">
                
              </span>
            </div>
          </div>
          
          {/*  */}
          <div className="mb-4">
            <label htmlFor="maxParticipants" className="block text-sm font-medium text-gray-700 mb-1">
              <span className="text-red-500 ml-1">*</span>
            </label>
            <select
              id="maxParticipants"
              name="maxParticipants"
              value={formData.maxParticipants}
              onChange={handleInputChange}
              className="w-full px-3 py-2 border border-gray-300 rounded-md"
            >
              {[1, 2, 3, 4, 5, 6, 8, 10, 12, 15, 20].map(num => (
                <option key={num} value={num}>{num}</option>
              ))}
            </select>
          </div>
          
          {/*  */}
          <div className="mb-4">
            <label className="block text-sm font-medium text-gray-700 mb-1">
              <span className="text-red-500 ml-1">*</span>
            </label>
            <div className="flex gap-2">
              {daysOfWeek.map((day, index) => (
                <button
                  key={day}
                  type="button"
                  onClick={() => handleDayToggle(index)}
                  className={`w-10 h-10 rounded-full flex items-center justify-center text-sm ${
                    formData.availableDays.includes(index)
                      ? 'bg-blue-600 text-white'
                      : 'bg-gray-100 text-gray-700 hover:bg-gray-200'
                  }`}
                >
                  {day}
                </button>
              ))}
            </div>
            <p className="text-xs text-gray-500 mt-1">
               
            </p>
          </div>
        </div>
        
        {/*  */}
        <div className="bg-white rounded-lg shadow p-6">
          <h2 className="text-xl font-bold mb-4"></h2>
          
          {/*  */}
          <div className="mb-4">
            <label htmlFor="includesItems" className="block text-sm font-medium text-gray-700 mb-1">
              
            </label>
            <textarea
              id="includesItems"
              name="includesItems"
              value={formData.includesItems}
              onChange={handleInputChange}
              className="w-full px-3 py-2 border border-gray-300 rounded-md"
              rows={3}
              placeholder=": "
            />
          </div>
          
          {/*  */}
          <div className="mb-4">
            <label htmlFor="requiredItems" className="block text-sm font-medium text-gray-700 mb-1">
              
            </label>
            <textarea
              id="requiredItems"
              name="requiredItems"
              value={formData.requiredItems}
              onChange={handleInputChange}
              className="w-full px-3 py-2 border border-gray-300 rounded-md"
              rows={3}
              placeholder=": "
            />
          </div>
          
          {/*  */}
          <div className="mb-4">
            <label htmlFor="cancellationPolicy" className="block text-sm font-medium text-gray-700 mb-1">
              <span className="text-red-500 ml-1">*</span>
            </label>
            <select
              id="cancellationPolicy"
              name="cancellationPolicy"
              value={formData.cancellationPolicy}
              onChange={handleInputChange}
              className="w-full px-3 py-2 border border-gray-300 rounded-md"
            >
              <option value="flexible"> (24)</option>
              <option value="moderate"> (324)</option>
              <option value="strict"> (73)</option>
            </select>
          </div>
        </div>
        
        {/*  */}
        <div className="flex justify-end gap-3">
          <button
            type="button"
            onClick={handleCancel}
            className="px-4 py-2 bg-gray-100 text-gray-700 rounded-lg hover:bg-gray-200"
            disabled={loading}
          >
            
          </button>
          <button
            type="submit"
            className="px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 flex items-center"
            disabled={loading}
          >
            {loading ? (
              <>
                <Loader className="w-4 h-4 animate-spin mr-2" />
                ...
              </>
            ) : (
              ''
            )}
          </button>
        </div>
      </form>
    </div>
  );
};

export default CreateExperienceForm;
