import React, { useState } from 'react';
import { FormStatusType } from '../../../types/attender/index';
import { ArrowRight, X, Smartphone, Monitor } from 'lucide-react';

interface TutorialOverlayProps {
  formStatus?: FormStatusType;
  isVisible: boolean;
  onClose: () => void;
  onComplete: () => void;
  onSkipAll?: () => void;
}

/**
 * 
 * 
 * 
 */
const TutorialOverlay: React.FC<TutorialOverlayProps> = ({
  formStatus = 'required',
  isVisible,
  onClose,
  onComplete,
  onSkipAll = () => {}
}) => {
  const [currentStep, setCurrentStep] = useState(1);
  const [isMobile, setIsMobile] = useState(window.innerWidth < 768);
  
  // 
  const totalSteps = formStatus === 'required' ? 3 : 5;
  
  // 
  const nextStep = () => {
    if (currentStep < totalSteps) {
      setCurrentStep(currentStep + 1);
    } else {
      onComplete();
    }
  };
  
  // 
  const prevStep = () => {
    if (currentStep > 1) {
      setCurrentStep(currentStep - 1);
    }
  };
  
  // 
  const getDeviceIcon = () => {
    return isMobile ? (
      <Smartphone className="w-5 h-5 mr-2" />
    ) : (
      <Monitor className="w-5 h-5 mr-2" />
    );
  };
  
  // 
  const getBasicRegistrationTutorial = () => {
    switch (currentStep) {
      case 1:
        // 
  if (!isVisible) return null;
  
  // 
  if (!isVisible) return null;
  
  return (
          <div>
            <h3 className="flex items-center text-lg font-bold mb-3">
              {getDeviceIcon()}
              : 
            </h3>
            <p className="mb-3">
              
            </p>
            <div className="bg-blue-50 border-l-4 border-blue-500 p-3 mb-3">
              <h4 className="font-bold"></h4>
              <ul className="list-disc list-inside text-sm mt-1">
                <li></li>
                <li></li>
                <li></li>
              </ul>
            </div>
            <p className="text-sm text-gray-600">
              
            </p>
          </div>
        );
      case 2:
        return (
          <div>
            <h3 className="flex items-center text-lg font-bold mb-3">
              
            </h3>
            <p className="mb-3">
              
            </p>
            <div className="bg-yellow-50 border-l-4 border-yellow-500 p-3 mb-3">
              <h4 className="font-bold"></h4>
              <p className="text-sm mt-1">
                1
              </p>
            </div>
          </div>
        );
      case 3:
        return (
          <div>
            <h3 className="flex items-center text-lg font-bold mb-3">
              
            </h3>
            <p className="mb-3">
              
            </p>
            <div className="grid grid-cols-2 gap-3 mb-3">
              <div className="bg-green-50 border border-green-200 rounded p-2 text-sm">
                <h4 className="font-bold">:</h4>
                <ul className="list-disc list-inside mt-1 text-xs">
                  <li></li>
                  <li></li>
                  <li></li>
                </ul>
              </div>
              <div className="bg-indigo-50 border border-indigo-200 rounded p-2 text-sm">
                <h4 className="font-bold">:</h4>
                <ul className="list-disc list-inside mt-1 text-xs">
                  <li></li>
                  <li></li>
                  <li></li>
                </ul>
              </div>
            </div>
            <p className="text-sm text-gray-600">
              echo
            </p>
          </div>
        );
      default:
        return null;
    }
  };
  
  // 
  const getFullRegistrationTutorial = () => {
    switch (currentStep) {
      case 1:
        return (
          <div>
            <h3 className="flex items-center text-lg font-bold mb-3">
              {getDeviceIcon()}
              : 
            </h3>
            <p className="mb-3">
              
            </p>
            <div className="grid grid-cols-1 sm:grid-cols-2 gap-3 mb-3">
              <div className="bg-blue-50 border border-blue-200 rounded p-2 text-sm">
                <h4 className="font-bold">:</h4>
                <ul className="list-disc list-inside mt-1 text-xs">
                  <li></li>
                  <li></li>
                  <li></li>
                </ul>
              </div>
              <div className="bg-green-50 border border-green-200 rounded p-2 text-sm">
                <h4 className="font-bold">:</h4>
                <ul className="list-disc list-inside mt-1 text-xs">
                  <li></li>
                  <li></li>
                  <li></li>
                </ul>
              </div>
            </div>
            <p className="text-sm text-gray-600">
              
            </p>
          </div>
        );
      case 2:
        return (
          <div>
            <h3 className="flex items-center text-lg font-bold mb-3">
              
            </h3>
            <p className="mb-3">
              
            </p>
            <div className="bg-blue-50 border-l-4 border-blue-500 p-3 mb-3">
              <h4 className="font-bold"></h4>
              <ul className="list-disc list-inside text-sm mt-1">
                <li></li>
                <li></li>
                <li></li>
              </ul>
            </div>
          </div>
        );
      case 3:
        return (
          <div>
            <h3 className="flex items-center text-lg font-bold mb-3">
              
            </h3>
            <p className="mb-3">
              
            </p>
            <div className="mb-3">
              <div className="bg-green-50 border border-green-200 rounded-t p-3">
                <h4 className="font-bold">:</h4>
                <ul className="list-disc list-inside text-sm mt-1">
                  <li></li>
                  <li></li>
                  <li></li>
                </ul>
              </div>
              <div className="bg-red-50 border border-red-200 rounded-b p-3">
                <h4 className="font-bold">:</h4>
                <ul className="list-disc list-inside text-sm mt-1">
                  <li></li>
                  <li></li>
                  <li></li>
                </ul>
              </div>
            </div>
            <p className="text-sm text-gray-600 italic">
              
            </p>
          </div>
        );
      case 4:
        return (
          <div>
            <h3 className="flex items-center text-lg font-bold mb-3">
              
            </h3>
            <p className="mb-3">
              
            </p>
            <div className="bg-yellow-50 border-l-4 border-yellow-500 p-3 mb-3">
              <h4 className="font-bold"></h4>
              <p className="text-sm mt-1">
                
              </p>
            </div>
            <p className="text-sm text-gray-600">
              
            </p>
          </div>
        );
      case 5:
        return (
          <div>
            <h3 className="flex items-center text-lg font-bold mb-3">
              
            </h3>
            <p className="mb-3">
              
            </p>
            <div className="grid grid-cols-1 gap-3 mb-3">
              <div className="bg-blue-50 border border-blue-200 rounded p-3 text-sm">
                <h4 className="font-bold">:</h4>
                <ol className="list-decimal list-inside mt-1">
                  <li className="mb-1">1-3</li>
                  <li className="mb-1"></li>
                  <li className="mb-1"></li>
                  <li></li>
                </ol>
              </div>
            </div>
            <div className="bg-green-50 border-l-4 border-green-500 p-3">
              <p className="text-sm italic">
                echo
              </p>
            </div>
          </div>
        );
      default:
        return null;
    }
  };
  
  // 
  const getTutorialContent = () => {
    if (formStatus === 'required') {
      return getBasicRegistrationTutorial();
    } else {
      return getFullRegistrationTutorial();
    }
  };
  
  // 
  if (!isVisible) return null;

  return (
    <div className="fixed inset-0 bg-black bg-opacity-50 z-50 flex justify-center items-center p-4">
      <div className="bg-white rounded-lg shadow-xl max-w-xl w-full max-h-[90vh] overflow-y-auto">
        {/*  */}
        <div className="sticky top-0 bg-white p-4 border-b border-gray-200 flex justify-between items-center">
          <h2 className="text-xl font-bold">
            {formStatus === 'required' ? '' : ''}
          </h2>
          <div className="flex items-center space-x-2">
            {/*  */}
            <button
              onClick={() => setIsMobile(!isMobile)}
              className="text-gray-500 hover:text-gray-700 p-2"
              title={isMobile ? '' : ''}
            >
              {isMobile ? <Monitor size={20} /> : <Smartphone size={20} />}
            </button>
            
            {/*  */}
            <button 
              onClick={onClose}
              className="text-gray-500 hover:text-gray-700"
            >
              <X size={24} />
            </button>
          </div>
        </div>
        
        {/*  */}
        <div className="p-6">
          {getTutorialContent()}
        </div>
        
        {/*  */}
        <div className="sticky bottom-0 bg-white p-4 border-t border-gray-200">
          {/*  */}
          <div className="w-full bg-gray-200 rounded-full h-1.5 mb-4">
            <div 
              className="bg-blue-600 h-1.5 rounded-full transition-all duration-300" 
              style={{ width: `${(currentStep / totalSteps) * 100}%` }}
            ></div>
          </div>
          
          <div className="flex justify-between items-center">
            {/*  */}
            <div className="text-sm text-gray-500">
               {currentStep}/{totalSteps}
            </div>
            
            <div className="flex space-x-3">
              {/*  */}
              <button
                onClick={onSkipAll}
                className="text-sm text-gray-500 underline"
              >
                
              </button>
              
              {/*  */}
              <div className="flex space-x-2">
                {currentStep > 1 && (
                  <button
                    onClick={prevStep}
                    className="px-3 py-1 border border-gray-300 rounded-md hover:bg-gray-50"
                  >
                    
                  </button>
                )}
                
                <button
                  onClick={nextStep}
                  className="px-4 py-1 bg-blue-600 text-white rounded-md hover:bg-blue-700 flex items-center"
                >
                  {currentStep < totalSteps ? (
                    <> <ArrowRight className="w-4 h-4 ml-1" /></>
                  ) : (
                    ''
                  )}
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default TutorialOverlay;