/**
 * API
 *
 * Firebase AuthenticationAPI
 */

// Firebase Auth
let getAuth: () => any;
let onAuthStateChanged: (auth: any, callback: (user: any) => void) => void;
let getIdToken: (user: any, forceRefresh?: boolean) => Promise<string>;

try {
  // Firebase Auth
  const firebaseAuth = require('firebase/auth');
  getAuth = firebaseAuth.getAuth;
  onAuthStateChanged = firebaseAuth.onAuthStateChanged;
  getIdToken = firebaseAuth.getIdToken;
} catch (e) {
  // Firebase
  console.warn('Firebase Auth is not available, using mock functions');
  getAuth = () => ({ currentUser: { uid: 'mock-user-id' } });
  onAuthStateChanged = (_, callback) => callback(null);
  getIdToken = () => Promise.resolve('mock-token');
}

import { isDevelopment, isDebugMode } from "../config/env";
import api, {
  ApiResponse,
  ApiClient,
  ApiOptions,
  logApiRequest as apiClientLogApiRequest,
  logApiResponse as apiClientLogApiResponse,
} from "./apiClient";

// Cloud Run API
export const cloudRunConfig = {
  baseUrl: process.env.REACT_APP_CLOUD_RUN_API_URL || 'https://api.echo-cloud.example',
  timeout: 60000, // 60
  maxRetries: 3,
  retryDelay: 1000,
  headers: {
    'X-API-Version': '1.0'
  }
};

// API
const API_TIMEOUT = 30000; // 30

// 
let cachedToken: string | null = null;
let tokenExpiryTime: number = 0;

/**
 * Firebase
 *
 * @returns null
 */
export async function getAuthToken(): Promise<string | null> {
  try {
    const auth = getAuth();
    const user = auth.currentUser;

    if (!user) {
      return null;
    }

    // 
    const now = Date.now();
    if (cachedToken && tokenExpiryTime > now) {
      return cachedToken;
    }

    // 
    const token = await getIdToken(user, true);

    // 50
    cachedToken = token;
    tokenExpiryTime = now + 50 * 60 * 1000;

    return token;
  } catch (error) {
    console.error(":", error);
    return null;
  }
}

/**
 * 
 */
export function setupAuthListener(): void {
  const auth = getAuth();
  onAuthStateChanged(auth, (user) => {
    if (!user) {
      // 
      cachedToken = null;
      tokenExpiryTime = 0;
    }
  });
}

/**
 * 
 *
 * @param url URL
 * @param options 
 * @returns 
 */
async function request<T = any>(
  url: string,
  options: {
    method: string;
    body?: any;
    headers?: Record<string, string>;
    timeout?: number;
  } = { method: "GET" }
): Promise<ApiResponse<T>> {
  try {
    // 
    const controller = new AbortController();
    const timeoutId = setTimeout(() => controller.abort(), options.timeout || API_TIMEOUT);

    // 
    const token = await getAuthToken();

    // 
    const headers: Record<string, string> = {
      "Content-Type": "application/json",
      ...options.headers,
    };

    // 
    if (token) {
      headers["Authorization"] = `Bearer ${token}`;
    }

    // CSRF
    const csrfToken = document
      .querySelector('meta[name="csrf-token"]')
      ?.getAttribute("content");
    if (csrfToken) {
      headers["X-CSRF-Token"] = csrfToken;
    }

    // 
    const mergedOptions: RequestInit = {
      method: options.method,
      headers,
      body: options.body,
      credentials: "include", // 
      signal: controller.signal,
    };

    // 
    const response = await fetch(url, mergedOptions);
    clearTimeout(timeoutId);

    // 
    const responseHeaders = response.headers;
    const contentType = responseHeaders.get("content-type") || "";

    // 
    let data;
    if (contentType.includes("application/json")) {
      data = await response.json();
    } else if (contentType.includes("text/")) {
      data = await response.text();
    } else {
      // 
      data = await response.blob();
    }

    // 
    if (response.ok) {
      return {
        success: true,
        data,
        status: response.status,
        headers: responseHeaders,
      };
    }

    // 
    let errorData = {
      code: "UNKNOWN_ERROR",
      message: "An unknown error occurred",
    };

    if (typeof data === "object" && data !== null) {
      errorData = {
        ...errorData,
        ...data,
      };
    }

    // 401
    if (response.status === 401) {
      cachedToken = null;
      tokenExpiryTime = 0;
    }

    return {
      success: false,
      error: errorData,
      status: response.status,
      headers: responseHeaders,
    };
  } catch (error) {
    console.error("API request error:", error);

    // AbortController
    if (error instanceof DOMException && error.name === "AbortError") {
      return {
        success: false,
        error: {
          code: "TIMEOUT",
          message: "Request timed out",
        },
        status: 0,
        headers: new Headers(),
      };
    }

    // 
    return {
      success: false,
      error: {
        code: "NETWORK_ERROR",
        message: error instanceof Error ? error.message : "Network error",
        details: error,
      },
      status: 0,
      headers: new Headers(),
    };
  }
}

/**
 * GET
 */
async function get<T = any>(
  url: string,
  options: ApiOptions = {}
): Promise<ApiResponse<T>> {
  // URL
  const queryParams = new URLSearchParams();
  if (options.params) {
    Object.entries(options.params).forEach(([key, value]) => {
      if (value !== undefined && value !== null) {
        queryParams.append(key, String(value));
      }
    });
  }

  const queryString = queryParams.toString();
  const fullUrl = queryString ? `${url}?${queryString}` : url;

  return request<T>(fullUrl, {
    method: "GET",
    headers: options.headers,
    timeout: options.timeout,
  });
}

/**
 * POST
 */
async function post<T = any>(
  url: string,
  data?: any,
  options: ApiOptions = {}
): Promise<ApiResponse<T>> {
  return request<T>(url, {
    method: "POST",
    body: data ? JSON.stringify(data) : undefined,
    headers: options.headers,
    timeout: options.timeout,
  });
}

/**
 * PUT
 */
async function put<T = any>(
  url: string,
  data?: any,
  options: ApiOptions = {}
): Promise<ApiResponse<T>> {
  return request<T>(url, {
    method: "PUT",
    body: data ? JSON.stringify(data) : undefined,
    headers: options.headers,
    timeout: options.timeout,
  });
}

/**
 * PATCH
 */
async function patch<T = any>(
  url: string,
  data?: any,
  options: ApiOptions = {}
): Promise<ApiResponse<T>> {
  return request<T>(url, {
    method: "PATCH",
    body: data ? JSON.stringify(data) : undefined,
    headers: options.headers,
    timeout: options.timeout,
  });
}

/**
 * DELETE
 */
async function del<T = any>(
  url: string,
  options: ApiOptions = {}
): Promise<ApiResponse<T>> {
  return request<T>(url, {
    method: "DELETE",
    headers: options.headers,
    timeout: options.timeout,
  });
}

/**
 * POST
 */
async function uploadFile<T = any>(
  url: string,
  file: File,
  fieldName: string = "file",
  additionalData: Record<string, any> = {},
  options: ApiOptions = {},
  progressCallback?: (progress: number) => void
): Promise<ApiResponse<T>> {
  try {
    // 
    const token = await getAuthToken();

    const formData = new FormData();
    formData.append(fieldName, file);

    // 
    Object.entries(additionalData).forEach(([key, value]) => {
      formData.append(key, String(value));
    });

    // XMLHttpRequest
    if (progressCallback) {
      return new Promise((resolve, reject) => {
        const xhr = new XMLHttpRequest();

        xhr.upload.addEventListener("progress", (event) => {
          if (event.lengthComputable) {
            const progress = Math.round((event.loaded / event.total) * 100);
            progressCallback(progress);
          }
        });

        xhr.addEventListener("load", () => {
          if (xhr.status >= 200 && xhr.status < 300) {
            let data;
            try {
              data = JSON.parse(xhr.responseText);
            } catch (e) {
              data = xhr.responseText;
            }

            resolve({
              success: true,
              data,
              status: xhr.status,
              headers: new Headers(
                xhr
                  .getAllResponseHeaders()
                  .split("\r\n")
                  .filter(Boolean)
                  .reduce((acc, header) => {
                    const [name, value] = header.split(": ");
                    if (name && value) {
                      acc[name.toLowerCase()] = value;
                    }
                    return acc;
                  }, {} as Record<string, string>)
              ),
            });
          } else {
            let errorData;
            try {
              errorData = JSON.parse(xhr.responseText);
            } catch (e) {
              errorData = { message: "Unknown error" };
            }

            resolve({
              success: false,
              error: {
                code: `HTTP_${xhr.status}`,
                message: errorData.message || "Request failed",
                details: errorData,
              },
              status: xhr.status,
              headers: new Headers(
                xhr
                  .getAllResponseHeaders()
                  .split("\r\n")
                  .filter(Boolean)
                  .reduce((acc, header) => {
                    const [name, value] = header.split(": ");
                    if (name && value) {
                      acc[name.toLowerCase()] = value;
                    }
                    return acc;
                  }, {} as Record<string, string>)
              ),
            });
          }
        });

        xhr.addEventListener("error", () => {
          reject(new Error("Network error occurred"));
        });

        xhr.addEventListener("abort", () => {
          reject(new Error("Request was aborted"));
        });

        xhr.addEventListener("timeout", () => {
          reject(new Error("Request timed out"));
        });

        xhr.open("POST", url);

        // 
        xhr.timeout = options.timeout || API_TIMEOUT;

        // 
        if (token) {
          xhr.setRequestHeader("Authorization", `Bearer ${token}`);
        }

        // CSRF
        const csrfToken = document
          .querySelector('meta[name="csrf-token"]')
          ?.getAttribute("content");
        if (csrfToken) {
          xhr.setRequestHeader("X-CSRF-Token", csrfToken);
        }

        // 
        if (options.headers) {
          Object.entries(options.headers).forEach(([name, value]) => {
            if (typeof value === "string") {
              xhr.setRequestHeader(name, value);
            }
          });
        }

        xhr.send(formData);
      });
    }

    // fetch
    return request<T>(url, {
      method: "POST",
      body: formData,
      headers: {
        ...(options.headers || {}),
        ...(token ? { Authorization: `Bearer ${token}` } : {})
      },
      timeout: options.timeout
    });
  } catch (error) {
    console.error(":", error);
    return {
      success: false,
      error: {
        code: "UPLOAD_ERROR",
        message:
          error instanceof Error
            ? error.message
            : "",
        details: error,
      },
      status: 0,
      headers: new Headers(),
    };
  }
}

// API
const enhancedApiClient: ApiClient = {
  get,
  post,
  put,
  patch,
  delete: del, // 'delete'JavaScriptdel
  uploadFile,
};

// API 
const client = enhancedApiClient;
const cloudRunApiClient = enhancedApiClient; // Cloud Run

// 
setupAuthListener();

//  - isolatedModules
export type { ApiResponse };

// 
/**
 * API
 */
export function logApiRequest(method: string, url: string, data?: any): void {
  apiClientLogApiRequest(method, url, data);
}

/**
 * API
 */
export function logApiResponse<T = any>(method: string, url: string, response: ApiResponse<T>): void {
  apiClientLogApiResponse(method, url, response);
}

export default {
  client,
  cloudRunApiClient,
  getAuthToken,
  setupAuthListener
};